#cast-view{
    display: grid;
    border: solid 1px black;
    grid-template-columns: 1fr 1fr;
    background: var(--light-blue);
    font-size: 0.8em;   

}

#cast-view-info{
    padding: 3em;
    background: var(--light-box-background);
}

#cast-view-info *{
    margin: 0.5em;
    padding: 1em;
}


#cast-view-info #biography{
    height: 20em;
    padding: 2em;
    overflow: auto;
    background: var(--blue-focus);
}

#cast-view #cast-view-photo{
    text-align: center;
}

#cast-image{
    padding: 2em;
    background: white;
}