.movieTitle
{
    text-align: center;
    font-weight: bolder;
}

.FiltersWithMovie
{
    display: grid;
    grid-template-columns: 1fr 7fr;
    grid-auto-rows: auto;
    grid-gap: 10px;
}

.MovieList{
    padding: 1em 3em 1em 3em;
    background-color: var(--light-blue);
}

.listBox
{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-auto-rows: auto;
    grid-gap: 10px;
    height: 100vh;
    overflow: auto;
}

.movieFilter h1
{
    text-align: center;
}

.movieFilter{
    font-size: 0.8em;
}

/* .movieFilter *{
    border: 1px solid black;
} */

@media only screen and (max-width: 1250px){
    .listBox
    {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}

