.App {
  text-align: center;
}

.header{
  display: grid;
  grid-template-columns: 50% 50%;
  background-color: var(--blue-focus);
}

.header-logo {
  grid-column: 1/span 1;
}

.aboutBtn {
  grid-column: 2/span 1;
}
.aboutBtn button{
  float: right;
  background-color: white;
  border: 1px solid var(--orange-focus);
  border-radius: 5px;
  color: black;
  padding: 0.5rem 1rem;
  text-align: center;
  margin: 1.5rem 0.5rem;
  transition-duration: 0.4s;
  cursor: pointer;
}

.aboutBtn button:hover{
  background-color: var(--orange-focus);
  color: white;
}

.App-logo {
  height: 3.5rem;
  margin: 0.5rem;
  pointer-events: pointer;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main {
  position: absolute;
  left: 0;
  right: 0;
}

/* Used this Tutorial https://www.youtube.com/watch?v=NUQkajBdnmQ , https://github.com/Ihatetomatoes/react-router-page-transition-css */
/* simple - enter transition 300ms, exit 150ms */
.fade-appear,
.fade-enter {
    opacity: 0;
    z-index: 1;
}
.fade-appear-active,
.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 300ms linear 150ms;
}

.fade-exit {
    opacity: 1;
}

.fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 150ms linear;
}