.bm-item:hover {
  color: white;
}

.bm-burger-button {
  position: absolute;
  width: 36px;
  height: 30px;
  left: 50px;
  top: 13em;
}

.bm-burger-bars {
  background: #373a47;
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #bdc3c7;
}

.bm-menu {
  position: relative;
  background: #373a47;
  padding: 2.5em 1.5em 6em 1.5em;
  font-size: 1em;
}

/* .bm-morph-shape {
  fill: #373a47;
} */

.bm-item-list {
  color: #b8b7ad;
  margin: 0em;
  padding: 0em;
}

:root{
  --dark-box-background: rgb(34, 51, 66);
  --light-box-background: rgb(57, 85, 110);
  --dark-box-elements: rgb(22, 33, 43);
  --light-box-elements: rgb(49, 67, 83);
  --blue-focus: rgb(172, 200, 223);
  --orange-focus: rgb(255, 164, 89);
  --light-orange-focus: rgb(247, 197, 157);
  --light-blue: rgb(222, 227, 236);
}
